<template>
    <div>
        <div class="header">
            <span @click="changeShow">导航</span>
            <span>{{title || '后端管理'}}</span>
            <div @click="logOutShow">
                <span v-if="isLogOutShow">{{userName}}</span>
                <span @click="logOut" v-else>退出</span>
            </div>
        </div>
        <c-nav :list="items" v-if="isNavShow" @my-event="changeShow"></c-nav>
    </div>
</template>

<script>
import Vue from "vue";
import Cnav from "../nav";
import datas from "../../libs/data";

export default Vue.component("c-header", {
  component: { Cnav },
  props: ["param"],
  data() {
    return {
      title: this.$route.meta.title,
      userName: '',
      items: datas.list,
      isNavShow: this.$route.meta.isNavShow,
      isLogOutShow: true
    };
  },
  methods: {
    logOut() {
      this.axios.logOut().then(res => {
          this.$router.push('/logins')
        this.Toast(res.returnMsg);
      });
    },
    changeShow() {
        this.isNavShow = !this.isNavShow;
        console.log(this.isNavShow)
    },
    logOutShow() {
      this.isLogOutShow = !this.isLogOutShow;
    },
    query(){
        const userInfo = JSON.parse(this.Storage.sessionGet('USERINFO'));
        console.log(userInfo)
        if(userInfo){
            this.userName = userInfo.userName;
        }
    }
  },
  mounted() {
    this.query()
  }
});
</script>




